<template>
  <div>
  <loading :active.sync="isLoading"></loading>

  <form class="form-signin mb-5" @submit.prevent="signin">

    <h1 class="h3 mb-3 font-weight-normal text-center p-3">
      <i class="fas fa-kiwi-bird"></i>
        ... 請登入 ...
      <i class="fas fa-kiwi-bird"></i>
      </h1>
    <label for="inputEmail" class="sr-only">Email address</label>
    <input v-model="user.username" type="email" id="inputEmail" class="form-control" placeholder="Email address" required autofocus>
    <label for="inputPassword" class="sr-only">Password</label>
    <input v-model="user.password" type="password" id="inputPassword" class="form-control" placeholder="Password" required> 
    <button class="btn btn-lg btn-primary btn-block" type="submit" >Sign in</button>
   
  </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      user:{
        username:'',
        password: ''
      },
      isLoading:false
    }
  },
  methods: {
    signin(){
      const vm = this
      const api =`${process.env.APIPATH}/admin/signin`
      // console.log(api)
      vm.isLoading= true
      this.$http.post(api ,vm.user).then((response) => {
        // console.log(response.data)
        if(response.data.success){
          vm.isLoading = false
          vm.$router.push('/admin/products')
        }
      })
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body {
  height: 100vh;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;  
  margin:  auto;  
  margin-top: 64px;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



